.settings{display:flex;flex-direction:column;flex-wrap:nowrap;width:100%}.settings .settings__header{display:inline-block;align-self:start;color:#4a4a4a;font-family:Heebo Light;font-size:25px;line-height:37px;font-weight:300;margin:0 0 20px 0}@media (max-width: 720px){.settings .settings__header{margin-left:32px}}.settings .settings__content{width:100%;flex-grow:1;box-shadow:0 0 4px rgba(0,0,0,0.04);border-radius:3px;background-color:#fff;display:flex;flex-direction:column;flex-wrap:nowrap}.settings .settings__content .settings__tabs{width:100%;position:relative;display:flex;flex-wrap:nowrap;overflow-x:auto;overflow-y:hidden;flex-direction:row;align-items:center;min-height:51px;border-bottom:1px solid #e8e8e8}.settings .settings__content .settings__tabs .settings__tab{position:relative;white-space:nowrap;text-decoration:none;padding-top:13px;padding-left:30px;padding-right:30px;color:#97040c;font-family:Heebo;line-height:37px;font-weight:normal}.settings .settings__content .settings__tabs .settings__tab::after{content:"";position:absolute;background-color:#9f0008;height:2px;top:calc(100% - 2px);left:50%;right:50%;transition:left 0.15s, right 0.15s}.settings .settings__content .settings__tabs .settings__tab:hover::after{left:2px;right:2px}.settings .settings__content .settings__tabs .settings__tab_active{font-family:Heebo;font-weight:500;color:#4a4a4a}.settings .settings__content .settings__tabs .settings__tab_active::after{left:2px;right:2px}.settings .settings__content .settings__tab-content{flex-grow:1}

