.user{display:flex;flex-direction:row;justify-content:flex-start;align-items:center;justify-content:space-between;height:50px;position:relative}.user::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-color:#000;opacity:0.16;z-index:-1;transition:opacity 0.15s}.user .toggle-btn{font-size:100%;font-family:inherit;background-color:transparent;outline:none;border:0;padding:0;color:#fff;height:100%;border-left:1px solid #fff;width:50px;display:flex;align-items:center;cursor:pointer}.user .toggle-btn .toggle-btn__ico{width:1.35em;height:1.35em;margin:0 auto;transition:transform 0.15s ease-in-out}.user .toggle-btn .toggle-btn__ico svg{width:1.35em;height:1.35em;fill:#fff}.user .toggle-btn:hover .toggle-btn__ico{transform:scale(1.1)}.user .user__link{text-decoration:none;width:100%}.user .user__link:hover .user__avatar{transform:scale(1.1)}.user .user__link:hover .user__label{font-size:1.05em}.user .user__avatar{vertical-align:middle;width:32px;height:32px;border-radius:50%;margin-left:16px;transition:transform 0.15s ease-in-out}.user .user__label{font-family:Heebo;font-style:normal;font-weight:500;color:#fff;line-height:50px;margin-left:16px;transition:font-size 0.25s ease-in-out}

