*{font-family:Heebo,sans-serif;font-size:16px;box-sizing:border-box}.container{position:relative}.menu{background-color:red;width:200px;position:fixed;height:100vh}.content{min-height:100vh;margin-left:200px;width:calc(100% - 200px);background-color:green}

